<template>
  <v-app>
    <app-header></app-header>
    <v-content>
      <router-view></router-view>
    </v-content>
    <div class="text-center">
      <v-snackbar v-model="message" :timeout="timeout">
        {{ message }}
        <template v-slot:action="{ attrs }">
          <v-btn :color="color" text v-bind="attrs" @click="snackbar = false">
            Закрыть
          </v-btn>
        </template>
      </v-snackbar>
    </div>
  </v-app>
</template>
<script>
import AppHeader from './components/AppHeader';

export default {
  name: 'App',
  data(){
    return{
      snackbar: false,
      timeout: 4000,
    }
  },
  computed:{
    message: {
      get : function () {
        return this.$store.getters.getMessage
      },
      set: ()=>{}
    },
    color () {
      return this.$store.getters.getActiveColor
    },
  },
  components: {
    AppHeader,
  },
};
</script>
