<template>
  <v-app-bar>
    <router-link to="/" >
      <h1 class="logo">Галерея</h1>
    </router-link>
    <v-bottom-navigation
        v-model="value"
        height="64"
        :background-color="color"
        dark
        shift
    >
    <v-btn v-for="item in routes" :key = item.route :to="item.route">
      <span>{{item.title}}</span>
      <v-icon>{{item.icon}}</v-icon>
    </v-btn>
  </v-bottom-navigation>
  </v-app-bar>
</template>

<script>
export default {
name: "AppHeader",
  data(){
    return{
      value: 1,
      routes:[
        {
          title: 'Просмотр',
          icon: 'mdi-image',
          route: '/'
        },
        {
          title: 'Загрузить',
          icon: 'mdi-file-upload-outline',
          route: '/upload'
        },
        {
          title: 'Профиль',
          icon: 'mdi-account',
          route: '/profile'
        }
      ]
    }
  },
  computed: {
    color () {
      this.$store.commit('SET_ACTIVE_COLOR', this.value )
      return this.$store.getters.getActiveColor
    },
  },
}
</script>

<style scoped>
/deep/.v-toolbar__content {
  padding: 0;
}
.v-bottom-navigation.v-item-group.theme--dark.v-bottom-navigation--shift.teal {
  position: sticky;
  top: 0;
}
.v-sheet.theme--light.v-toolbar.v-app-bar {
  max-height: 64px;
}
/deep/.v-item-group.v-bottom-navigation {
  box-shadow: none !important;
}
.logo {
  position: absolute;
  z-index: 2;
  color: white;
  left: 30px;
  font-weight: 400;
  top: 10px
}
</style>