import cookie from 'vue-cookies'
export default{
    state: {
        user: {
            isAuthenticated: false,
            jwt: ''
        }
    },
    mutations: {
        SET_USER(state, payload){
            state.user.isAuthenticated = true
            state.user.jwt = payload
        },
        UNSET_USER(state){
            cookie.remove("jwt")
            state.user = {
                isAuthenticated: false,
                jwt: ''
            }
        }
    },
    actions: {
        SIGNUP({commit}, payload) {
            commit('SET_PROCESSING', true)
            commit('CLEAR_ERROR')
            const url = 'http://gallery/api/user/signup.php';
            const data = {
                password: payload.password,
                email: payload.email,
            };

            fetch(url, {
                method: 'POST',
                body: JSON.stringify(data),
                headers: {
                    'Content-Type': 'application/json',
                }
            })
            .then(response => {
                return response.json()
            })
            .then(json => {
                if (json.jwt != null) {
                    commit('SET_USER', json.jwt)
                    cookie.set("jwt", json.jwt)
                }
                if (json.message != null) {
                    commit('SET_MESSAGE', json.message)
                }
                if (json.error != null){
                    commit('SET_ERROR', json.error)
                }
                commit('SET_PROCESSING', false)
            })
        },
        SIGNIN({commit}, payload){
            commit('SET_PROCESSING', true)
            commit('CLEAR_ERROR')
            const url = 'http://gallery/api/user/signin.php';
            const data = {
                password: payload.password,
                email: payload.email,
            };

            fetch(url, {
                method: 'POST',
                body: JSON.stringify(data),
                headers: {
                    'Content-Type': 'application/json',
                }
            })
            .then(response => {
                return response.json()
            })
            .then(json => {
                if (json.jwt != null) {
                    commit('SET_USER', json.jwt)
                }
                if (json.message != null) {
                    commit('SET_MESSAGE', json.message)
                }
                if (json.error != null){
                    commit('SET_ERROR', json.error)
                }
                commit('SET_PROCESSING', false)
            })
        },
        SIGNOUT({commit}){
            commit('UNSET_USER')
        },
        STATE_CHANGED({commit}, payload){
            if (payload){
                commit('SET_PROCESSING', true)
                commit('CLEAR_ERROR')
                const url = 'http://gallery/api/user/validate.php';
                const data = {
                    jwt: payload
                };
                fetch(url, {
                    method: 'POST',
                    body: JSON.stringify(data),
                    headers: {
                        'Content-Type': 'application/json',
                    }
                })
                .then(response => {
                    return response.json()
                })
                .then(json => {
                    if (json.isValid) {
                        commit('SET_PROCESSING', false)
                        commit('SET_USER', payload)
                    }
                   else{
                        commit('SET_PROCESSING', false)
                        commit('UNSET_USER')
                    }
                })
            }
            else {
                commit('UNSET_USER')
            }
        }
    },
    getters:{
        getJwt: (state)=> state.user.jwt,
        isAuthenticated: (state) => state.user.isAuthenticated
    }
}
