export default {
    state:{
        images: []
    },
    mutations:{
        SET_IMAGES(state, payload){
            state.images = payload
        }
    },
    actions: {
        GET_IMAGES({commit}){
            commit('SET_PROCESSING', true)
            commit('CLEAR_ERROR')
            const url = 'http://gallery/api/product/getimages.php';
            fetch(url, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                }
            })
                .then(response => {
                    return response.json()
                })
                .then(json => {
                    if (json.images != null) {
                        commit('SET_PROCESSING', false)
                        commit('SET_IMAGES', json.images)
                    }
                    if (json.error != null){
                        commit('SET_PROCESSING', false)
                        commit('SET_ERROR', json.error)
                    }
                })
        },
        SEND_IMAGES({commit, getters}, payload){
            commit('SET_PROCESSING', true)
            commit('CLEAR_ERROR')
            const url = 'http://gallery/api/product/create.php';
            const data = {
                images: payload,
                jwt: getters.getJwt
            };
            fetch(url, {
                method: 'POST',
                body: JSON.stringify(data),
                headers: {
                    'Content-Type': 'application/json',
                }
            })
                .then(response => {
                    return response.json()
                })
                .then(json => {
                    if (json.message != null) {
                        commit('SET_PROCESSING', false)
                        commit('SET_MESSAGE', json.message)
                    }
                    if (json.error != null){
                        commit('SET_PROCESSING', false)
                        commit('SET_ERROR', json.error)
                    }
                })
        },
    },
    getters:{
        getImages: (state) =>{
            return state.images
        }
    }
}