import Vue from 'vue'
import App from './App.vue'
import router from './router/route'
import store from './store/store'
import vuetify from './plugins/vuetify';
import cookie from 'vue-cookies'

Vue.config.productionTip = false

new Vue({
  router,
  store,
  vuetify,
  render: h => h(App),
  created() {
    if ( cookie.isKey("jwt")){
      this.$store.dispatch('STATE_CHANGED', cookie.get("jwt")).then()
    }
    else{
      this.$store.dispatch('STATE_CHANGED').then()
    }
  }
}).$mount('#app')
